@import './fonts';
@import './modules/vars';
@import './modules/breakpoints';
@import './modules/colors';
@import './modules/functions';

:root {
  --height-header-mobile: 37px;
  --height-header-desktop: 79px;
}

*, *:before, *::before, *:after, *::after {
  box-sizing: border-box;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 500;
  padding: 0;
  margin: 0;
}

html, body {
  //height: 100vh; //для того, чтобы прижать футер к низу страницы
  line-height: 1;
  font-size: 14px;
  //color: #fff;
  font-family: $primaryFont;
  scroll-behavior: smooth;
}

html.lock, body.lock {
  //overflow-y: hidden;
  position: fixed;
  height: 100vh;
}

body {
  box-sizing: border-box;
  zoom: 1;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  position: relative;
  overflow-x: hidden;
  @include main-style-text;
}

html.lock, body.lock {
  overflow-y: hidden;
  position: fixed;
  height: 100vh;
}

.wrapper {
  min-height: 100%;
  min-height: -webkit-fill-available;
  display: flex;
  flex-direction: column;
  width: 100vw;
  //overflow-x: hidden;
}

ul, ol {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

td > ul {
  margin: 0;
}

p {
  margin: 0;
}

button {
  margin: 0;
  padding: 0;
  border: none;
  background-color: initial;
}

a {
  color: initial;
  text-decoration: none;

  &:hover {
    color: initial;
    text-decoration: none;
  }
}


input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  @media all and (min-width: $breakpoint-desktop-S) {
    padding: 0 80px;
    max-width: 1400px;
  }
}

.content {
  flex: 1 1 auto;
  color: $color-dark;
  position: relative;
  z-index: 1;
  margin-top: var(--height-header-mobile);
  @media all and (min-width: $breakpoint-tablet-L) {
    margin-top: var(--height-header-desktop);
  }
}

